<template>
  <div>
    <!--Schedule-->
    <v-flex xs10 class="hidden-xs-only">
      <h2 class="text-sm-center primary--text mb-3 ml-4">   </h2>
      <v-flex xs12 sm12 class="ml-4">
        <div class="secondary--text text-sm-center">
          <p class="pl-3 mb-4">- :
            <span class="schedule_time"> 9  13</span>
            <span></span>
            <span class="schedule_time"> 16  20</span>
          </p>
          <p>:
            <span class="schedule_time"> 9  13</span>
          </p>
          <p>:
            <span style="font-weight: bold;"></span>
          </p>
        </div>
      </v-flex>
    </v-flex>

    <!--Schedule mobile-->
    <div class="hidden-sm-and-up">
      <h2 class="primary--text">   </h2>
      <v-flex xs12 sm12 class="ml-4">
        <div class="secondary--text text-sm-center">
          <p class="mb-4">- : <br>
            <span class="schedule_time" style="line-height: 30px"> 9  13</span>
            <span></span>
            <span class="schedule_time"> 16  20</span>
          </p>
          <p>:
            <span class="schedule_time"> 9  13</span>
          </p>
          <p>:
            <span style="font-weight: bold;"></span>
          </p>
        </div>
      </v-flex>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'schedule'
  }
</script>

<style scoped>
  .schedule_time {
    border-radius: 5px;
    padding: 8px;
    background-color: #bced96;
    color: white;
  }
</style>



